.nm__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
#singInDiv{
    min-width: 50px;
    min-height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    border:1px solid white;
    cursor: pointer;
    border-radius: 5px;
    padding: 5px;
    gap:2px
}
#singInDiv span{
    color: white;
    font-family: var(--font-family);
}
#singInDiv:hover{
    background: radial-gradient(circle at 30% 25%, rgba(0, 40, 83, 1) 20%, rgba(4, 12, 24, 1) 100%);
}
.nm__img-logo{
    width: 50px;
    filter: invert(100%);
}
.nm__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.nm__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.nm__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.nm__navbar-links_container p,
.nm__navbar-sign p,
.nm__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.nm__navbar-sign-welcome{
    color: white;
    font-family: var(--font-family);
    margin-right:20px;
}
.nm__navbar-sign button,
.nm__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.nm__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.nm__navbar-menu svg {
    cursor: pointer;
}

.nm__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.nm__navbar-menu_container p {
    margin: 1rem 0;
}

.nm__navbar-menu_container-links-sign {
    display: none;
}

 @media screen and (max-width: 1050px) {
    .nm__navbar-links_container {
        display: none;
    }

    .nm__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .nm__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .nm__navbar {
        padding: 2rem;
    }

    .nm__navbar-sign {
        display: inline;
    }

    .nm__navbar-menu_container {
        top: 20px;
    }

    .nm__navbar-menu_container-links-sign {
        display: block;
    }
} 
@media screen and (max-width: 450px) {
    .nm__navbar-sign-welcome{
       display: none;
    }
}